import React from 'react';
import moment from 'moment';

import {
  Card,
  CardHeader,
  Col,
  Row,
  Table
} from 'reactstrap';

import SteamUser from '../../misc/steam-user';

class Component extends React.Component {
  render() {
    return (
      <Card className="shadow">
        <CardHeader className="border-0">
          <Row className="align-items-center">
            <Col className="col">
              <h3 className="mb-0"> ({this.props.active ? '' : ''} Bans)</h3>
            </Col>
          </Row>
        </CardHeader>
        <Table className="align-items-center table-flush" responsive>
          <thead className="thead-light">
          <tr>
            <th scope="col">GUID</th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col">IP?</th>
            <th scope="col"></th>
          </tr>
          </thead>
          <tbody>
          {
            this.props.bans.map((ban, key) => (
              <tr key={key}>
                <th scope="row">{ban.player.guid}</th>
                <td>{ban.privateReason}</td>
                <td>{ban.publicReason}</td>
                <td>{moment.utc(ban.startDate).format('YYYY/MM/DD HH:mm')}</td>
                <td>
                  {(ban.endDate ===  null) ? '' : moment.utc(ban.endDate).format('YYYY/MM/DD HH:mm')}
                  <br />
                  {(ban.unbannedDate !==  null) ? `(: ${moment.utc(ban.unbannedDate).format('YYYY/MM/DD HH:mm')})` : ''}
                </td>
                <td>
                  {(ban.ipBan) ? '' : ''}
                </td>
                <td>
                  <SteamUser steamUser={ban.admin} />
                </td>
              </tr>
            ))
          }
          </tbody>
        </Table>
      </Card>
    );
  }
}

export default Component;