export default [
  {
    type: 'start_server',
    name: '',
    toString: () => `.`
  },
  {
    type: 'stop_server',
    name: '',
    toString: () => `.`
  },
  {
    type: 'restart_server',
    name: '',
    toString: () => `.`
  },
  {
    type: 'add_admin_permission',
    name: '',
    toString: adminLog => ` ${adminLog.targetAdmin.displayName} .`
  },
  {
    type: 'update_admin_permission',
    name: '',
    toString: adminLog => ` ${adminLog.targetAdmin.displayName} .`
  },
  {
    type: 'remove_admin_permission',
    name: '',
    toString: adminLog => ` ${adminLog.targetAdmin.displayName} .`
  },
  {
    type: 'add_ban',
    name: '',
    toString: adminLog => `${adminLog.ipBanned ? 'IP' : ''}  ${adminLog.targetPlayer.guid} ${adminLog.length === -1 ? '' : `for ${adminLog.length} ${adminLog.length > 1 ? '' : ''}`} : ${adminLog.reason}`
  },
  {
    type: 'un_ban',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid} : ${adminLog.reason}`
  },
  {
    type: 'delete_ban',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid}  : ${adminLog.reason}`
  },
  {
    type: 'add_warning',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid} : ${adminLog.reason}`
  },
  {
    type: 'delete_warning',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid}  : ${adminLog.reason}`
  },
  {
    type: 'add_note',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid}  : ${adminLog.reason}`
  },
  {
    type: 'delete_note',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid}  : ${adminLog.reason}`
  },
  {
    type: 'adjust_gold',
    name: '',
    toString: adminLog => {
      switch (adminLog.adjustmentType) {
        case 'add':
          return ` ${adminLog.amount}  ${adminLog.remove ? '' : ''} ${adminLog.targetPlayer.guid}  ${adminLog.from === 'bankGold' ? '' : ''} : ${adminLog.reason}`;
        case 'remove':
          return ` ${adminLog.amount}  ${adminLog.remove ? '' : ''} ${adminLog.targetPlayer.guid}  ${adminLog.from === 'bankGold' ? '' : ''} : ${adminLog.reason}`;
        case 'transfer':
          return ` ${adminLog.amount}   ${adminLog.targetPlayer.guid}  ${adminLog.recipientPlayer.guid} : ${adminLog.reason}`;
        default:
          return '';
      }
    }
  },
  {
    type: 'strip_player',
    name: '',
    toString: adminLog => ` ${adminLog.targetPlayer.guid} : ${adminLog.reason}`
  },
  {
    type: 'wipe_player_name',
    name: '',
    toString: adminLog => `: ${adminLog.name}`
  }
];